import React from 'react';
import PropTypes from 'prop-types';
import PizzaBlock from "./PizzaBlock";



const Categories = React.memo(function Categories({activeCategory,items,onClickCategory}) {


    return (
        <div className="categories">
            <ul>
                <li className={activeCategory === null ? 'active' : ''} onClick={() => onClickCategory(null)} >–í—Å–µ</li>
                {items &&
                items.map((name,index) => (
                        <li className={activeCategory === index ? 'active' : ''} onClick={() => onClickCategory(index)} key={`${name}_${index}`}>{name}</li>
                    )
                )}
            </ul>
        </div>
    );
})

Categories.propTypes = {
   // activeCategory: PropTypes.oneOf(PropTypes.number.isRequired,null),
    items: PropTypes.arrayOf(PropTypes.string).isRequired
    //onClickCategory: PropTypes.func.isRequired
}

Categories.defaultProps = {
    activeCategory:null,
    items:[]
}

export default Categories;