import React from 'react';
import {Categories, SortPopup,PizzaBlock,LoadingBlock} from "../components";
import {useDispatch, useSelector} from "react-redux";
import { setCategory, setSortBy} from "../redux/actions/filters";
import {fetchPizzas} from "../redux/actions/pizzas";
import {addPizzaToCart} from "../redux/actions/cart";

const categoryName = ["–ú—è—Å–Ω—ã–µ", "–í–µ–≥–µ—Ç–∞—Ä–∏–∞–Ω—Å–∫–∞—è", "–ì—Ä–∏–ª—å", "–û—Å—Ç—Ä—ã–µ", "–ó–∞–∫—Ä—ã—Ç—ã–µ"]
const sortItems = [{name:'–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏', type:'rating', order: 'desc'}, {name:'—Ü–µ–Ω–µ', type:'price', order: 'desc'},
    {name:'–∞–ª—Ñ–∞–≤–∏—Ç—É', type:'name', order: 'asc'}]

function Home() {

    const items = useSelector(({pizzas}) => pizzas.items)
    const cartItems = useSelector(({cart}) => cart.items)
    const isLoaded = useSelector(({pizzas}) => pizzas.isLoaded)
    const {sortBy,category} = useSelector(({filters}) => filters)
    const dispatch = useDispatch()

    React.useEffect(() => {
        dispatch(fetchPizzas(category,sortBy))
    },[category,sortBy])

    const onSelectCategory = React.useCallback((index) => {
        dispatch(setCategory(index))
    },[])

    const onSelectSort = React.useCallback((obj) => {
        dispatch(setSortBy(obj))
    },[sortBy])

    const handleAddPizzaToCart = (obj) => {
        dispatch(addPizzaToCart(obj))
    }

    return (
        <div className="container">
            <div className="content__top">
                <Categories onClickCategory={onSelectCategory}
                            items={categoryName}
                            activeCategory = {category}/>
                <SortPopup items={sortItems} activeSortType={sortBy.type} onClickSortType={onSelectSort}/>
            </div>
            <h2 className="content__title">–í—Å–µ –ø–∏—Ü—Ü—ã</h2>
            <div className="content__items">
                {isLoaded
                ? items&&
                    items.map(obj => (
                        <PizzaBlock key={obj.id} {...obj}
                                    onClickAddPizza={handleAddPizzaToCart}
                        addedCount={cartItems[obj.id]&& cartItems[obj.id].items.length}
                        />
                    ))
                    : Array(12).fill(0).map((_, index) => <LoadingBlock key={index}/>)
                }


            </div>
        </div>

    );
}

export default Home;